import * as React from "react"
import { Link } from "gatsby"

const PostL = (item:any) => {
    
    return (
        <>
        <div className="pt-2 shadow-md dark:shadow-black border dark:border-gray-800 border-gray-300 transition-all outline-1 relative overflow-hidden rounded-md md:pl-2 flex flex-col gap-1 items-center md:items-start pb-2 select-none" key={item.frontmatter.id}>
        {item.frontmatter.title&&<Link to={"/posts/"+item.frontmatter.id} className="text-xl z-[1] transition-all font-semibold dark:text-gray-50">{item.frontmatter.title}</Link>||<div>Êú™Áü•</div>}
  <div className=" dark:text-gray-400 text-gray-700 flex flex-col gap-1 z-[1]">
  {item.frontmatter.tags&&<div className="flex flex-row gap-2 md:text-start text-center">{item.frontmatter.tags.map((item_:any,i:Number)=><Link key={item.frontmatter.id+'-'+item_.text+'-'+i} className="hover:text-gray-900 dark:hover:text-gray-100 transition-all" to={"/tags/"+item_}>üè∑Ô∏è&nbsp;{item_}</Link>)}</div>||<div>Êú™Áü•</div>}
  {item.frontmatter.date&&<div className="text-center md:text-start">üìÖ:{item.frontmatter.date}</div>||<div>Êú™Áü•</div>}
  </div>
  </div>
        </>
    )
}

export default PostL