import { HeadFC, Link, graphql } from "gatsby"
import * as React from "react"
import AppContent from "./AppContent"
import Head_ from "./Head_"
import PostL from "./PostL"

const TagsContent = ({ data, pageContext }: any) => {
    const post = data.allMarkdownRemark.nodes.map((item: any) => <div key={item.frontmatter.id}>{PostL(item)}</div>)
    return (
        <AppContent setTheme={undefined}>
            <div className="word-break select-none dark:text-gray-50 break-all"><div className="text-xl font-semibold md:text-start text-center">Ê†áÁ≠æ&nbsp;üè∑Ô∏è:{pageContext.tag}</div></div>
            <div className="flex flex-col gap-3 pt-4 text-center md:text-start">{post}</div>
        </AppContent>
    )
}

export const pageQuery = graphql`
query tag($tag:String) {
    allMarkdownRemark(filter:{frontmatter: { tags: { eq: $tag }}}) {
    nodes{
      frontmatter {
        date
        id
        title
        tags
      }
    }
    }
  }
  `

export const Head: HeadFC = ({ pageContext }: any) => <><title>Ê†áÁ≠æ | {pageContext.tag}</title><Head_ /><script dangerouslySetInnerHTML={{ __html: `if (localStorage.ti == undefined && window.matchMedia('(prefers-color-scheme:dark)').matches || localStorage.ti == 1 && window.matchMedia('(prefers-color-scheme:dark)').matches) {document.documentElement.classList.add('dark')} else if (localStorage.ti==0){document.documentElement.classList.add('dark')}` }}></script></>

export default TagsContent